<template>
  <div class="other">
    <van-nav-bar
        title="题集详情"
        fixed
        left-text="返回"
        left-arrow
        @click-left="$router.back()"
    />
    <div class="top">
      <img v-if="!isPlay" :src="workbookInfo.img || require('./../../../common/images/bg.png')" alt="">
      <section class="info"  v-if="JSON.stringify(selectTestInfo) !== '{}' && isPlay">
        <div class="title">
          （{{playIndex +1}}）【{{selectTestInfo.testInfo.type}}】{{selectTestInfo.testInfo.title}}
        </div>
<!--   答案     -->
<!--   单选题     -->
        <div v-if="selectTestInfo.testInfo.type === '单选题'" class="danxuan">
          <van-radio-group v-model="soloResult">
            <van-radio style="margin-bottom: 1rem;" v-for="(item, key) in selectTestInfo.optionList" :key="key" :name="item.id" shape="square">
              <span :style="{color: selectTestInfo.record.status !== 1 && item.isTrue === 1 ? '#11a429' : '#fff'}">{{key +1}}、{{item.content}}</span>
            </van-radio>
          </van-radio-group>
        </div>
<!--   多选题     -->
        <div v-if="selectTestInfo.testInfo.type === '多选题'" class="duoxuan">
          <van-checkbox-group v-model="moreResult" ref="checkboxGroup">
            <van-checkbox style="margin-bottom: 1rem;" v-for="(item, key) in selectTestInfo.optionList" :key="key" :name="item.id" shape="square">
              <span :style="{color: selectTestInfo.record.status !== 1 && item.isTrue === 1 ? '#11a429' : '#fff'}">{{key +1}}、{{item.content}}</span>
            </van-checkbox>
          </van-checkbox-group>
        </div>
<!--   简答题     -->
        <div v-if="selectTestInfo.testInfo.type === '简答题' && selectTestInfo.record.status !== 1" class="jianda">
          <ul>
            <li v-for="(item, key) in selectTestInfo.optionList" :key="key">
              答案：{{item.content}}
            </li>
          </ul>
        </div>
        <div class="bottom">
          <span @click="showResult" style="color: #1989fa;cursor: pointer;z-index: 99;">显示答案</span>
          <van-icon @click="collect" v-if="selectTestInfo.collect" style="float: right;" size="20" color="#ff5542" name="star" />
          <van-icon @click="collect" v-else style="float: right;" size="20" color="#f1f1f1" name="star" />
        </div>
      </section>
    </div>
    <van-tabs v-model="activeName">
      <van-tab title="题集介绍" name="base">
        <div class="intro" style="background-color: #f9f9f9;">
          <div class="top">
            <div class="title ellipsis">
              {{ workbookInfo.name }}
            </div>
            <div class="zone">
              共{{ testList.length }}题目
            </div>
            <div class="bottom">
              <div class="left">
                <van-tag size="medium" mark type="primary">{{ workbookInfo.type }}</van-tag>
              </div>
              <div class="right">
                {{ workbookInfo.readNum }}人在看
              </div>
            </div>
          </div>
          <div class="bottom">
            <van-divider content-position="left">我的题目浏览进度</van-divider>
            <van-progress style="margin-bottom: 2rem;margin-top: 2rem;" :percentage="testList.filter(i => i.record !== null).length === 0 ? 0 : Math.floor((testList.filter(item => item.record !== null).length / testList.length) * 100)" stroke-width="8" />
          </div>
        </div>
      </van-tab>
      <van-tab title="题目" name="zone">
        <section class="zone-list">
          <van-progress style="margin-bottom: 2rem;margin-top: 2rem;" :percentage="testList.filter(i => i.record !== null).length === 0 ? 0 : Math.floor((testList.filter(item => item.record !== null).length / testList.length) * 100)" stroke-width="8" />
          <ul>
            <li v-for="(item, key) in testList" :key="item.testInfo.id" :class="[playIndex === key ? 'select' : '']"
                @click="showTest(item, key)">
              <div class="left">
                <svg t="1611489192761" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7312" width="64" height="64"><path d="M912 624c8.837 0 16 7.163 16 16s-7.163 16-16 16c-61.855 0-112 50.145-112 112s50.145 112 112 112c8.837 0 16 7.163 16 16s-7.163 16-16 16H240c-79.529 0-144-64.471-144-144 0-55.73 31.662-104.062 77.977-128C127.662 616.063 96 567.73 96 512s31.662-104.062 77.977-128C127.662 360.062 96 311.73 96 256c0-79.529 64.471-144 144-144h672c8.837 0 16 7.164 16 16s-7.163 16-16 16c-61.855 0-112 50.144-112 112s50.145 112 112 112c8.837 0 16 7.164 16 16s-7.163 16-16 16c-61.855 0-112 50.144-112 112 0 61.855 50.145 112 112 112zM608 768V256c0-17.673-14.327-32-32-32H320c-17.673 0-32 14.327-32 32v528c0 8.837 7.164 16 16 16a15.94 15.94 0 0 0 11.456-4.843c0.048-0.046 0.1-0.085 0.147-0.133L448 662.627l132.396 132.397c0.048 0.048 0.101 0.087 0.148 0.134A15.943 15.943 0 0 0 592 800c8.837 0 16-7.163 16-16v-16z" p-id="7313" fill="#cdcdcd"></path></svg>
              </div>
              <div class="middle ellipsis">
                ({{item.testInfo.type}}){{ item.testInfo.title }}
              </div>
              <div class="right">
<!--  未答题              -->
                <span style="width: 2rem;height: 2rem;display: inline-block" v-if="!item.record">
                                    <svg t="1579078514943" class="icon" viewBox="0 0 1024 1024" version="1.1"
                                         xmlns="http://www.w3.org/2000/svg" p-id="3380" width="200" height="200"><path
                                        d="M512 512m-512 0a512 512 0 1 0 1024 0 512 512 0 1 0-1024 0Z" fill="#EBEBEB"
                                        p-id="3381"></path><path
                                        d="M512 512m-409.6 0a409.6 409.6 0 1 0 819.2 0 409.6 409.6 0 1 0-819.2 0Z"
                                        fill="#CCCCCC" p-id="3382"></path></svg>
                                </span>
<!--                -->
                <span style="width: 2rem;height: 2rem;display: inline-block" v-else>
<!--   答对               -->
                  <svg v-if="item.record.status === 1" t="1611489142075" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6454" width="64" height="64"><path d="M536.059471 918.462108a385.778196 385.778196 0 1 1 385.778196-385.778197 385.778196 385.778196 0 0 1-385.778196 385.778197z" fill="#24566D" p-id="6455"></path><path d="M288.059201 532.649467a248.000269 110.222342 0 1 0 496.000539 0 248.000269 110.222342 0 1 0-496.000539 0Z" fill="#FFFFFF" p-id="6456"></path><path d="M536.059471 532.649467m-82.666757 0a82.666756 82.666756 0 1 0 165.333513 0 82.666756 82.666756 0 1 0-165.333513 0Z" fill="#27BEC4" p-id="6457"></path><path d="M536.059471 532.649467m-55.111171 0a55.111171 55.111171 0 1 0 110.222341 0 55.111171 55.111171 0 1 0-110.222341 0Z" fill="#272528" p-id="6458"></path><path d="M515.392781 511.982778m-13.777792 0a13.777793 13.777793 0 1 0 27.555585 0 13.777793 13.777793 0 1 0-27.555585 0Z" fill="#FFFFFF" p-id="6459"></path></svg>

                  <svg v-if="item.record.status === 2" t="1611489221376" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="8176" width="64" height="64"><path d="M512 76.8c-236.8 0-435.2 192-435.2 435.2s192 435.2 435.2 435.2 435.2-192 435.2-435.2S748.8 76.8 512 76.8z m249.6 320L480 704c-12.8 12.8-38.4 12.8-51.2 0L288 556.8c-12.8-12.8-12.8-38.4 0-51.2 12.8-12.8 38.4-12.8 51.2 0l115.2 115.2L704 339.2c12.8-12.8 38.4-12.8 51.2 0 25.6 12.8 25.6 38.4 6.4 57.6z" fill="#68D279" p-id="8177"></path><path d="M710.4 339.2l-256 281.6-115.2-115.2c-12.8-12.8-38.4-12.8-51.2 0-12.8 12.8-12.8 38.4 0 51.2l147.2 140.8c12.8 12.8 38.4 12.8 51.2 0L768 390.4c12.8-12.8 12.8-38.4 0-51.2-19.2-12.8-44.8-12.8-57.6 0z" fill="#FFFFFF" p-id="8178"></path></svg>

<!--   答错               -->
                                    <svg v-if="item.record.status === 3" t="1611489247872" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="8945" width="64" height="64"><path d="M512 512m-435.2 0a435.2 435.2 0 1 0 870.4 0 435.2 435.2 0 1 0-870.4 0Z" fill="#FE6D68" p-id="8946"></path><path d="M563.2 512l108.8-108.8c12.8-12.8 12.8-38.4 0-51.2-12.8-12.8-38.4-12.8-51.2 0L512 460.8 403.2 352c-12.8-12.8-38.4-12.8-51.2 0-12.8 12.8-12.8 38.4 0 51.2L460.8 512 352 620.8c-12.8 12.8-12.8 38.4 0 51.2 12.8 12.8 38.4 12.8 51.2 0L512 563.2l108.8 108.8c12.8 12.8 38.4 12.8 51.2 0 12.8-12.8 12.8-38.4 0-51.2L563.2 512z" fill="#FFFFFF" p-id="8947"></path></svg>
                                </span>
                <span style="font-size: 1.2rem;color: #1989fa;">查看题目</span>
              </div>
            </li>
          </ul>
          <van-divider dashed>{{ testList.length === 0 ? '暂无相关题目' : '已经到底了' }}</van-divider>
        </section>
      </van-tab>
      <van-tab title="评论" name="answer">
        <section class="answer" v-if="JSON.stringify(selectTestInfo) !== '{}'">
          <div class="top">
            <van-field
                v-model="commentInfo.content"
                rows="2"
                autosize
                label="评论"
                type="textarea"
                maxlength="200"
                placeholder="请输入评论"
                show-word-limit
            />
            <div class="submit">
              <van-button style="width: 100%;"
                          @click="addComment">评论
              </van-button>
            </div>
          </div>
          <div class="answer-list">
            <ul>
              <li v-for="item in commentList" :key="item.commentInfo.id">
                <div class="top">
                  <van-button v-if="userInfo.id === item.userInfo.id"
                              style="position: absolute;right: 1rem;top: 2.3rem;" type="danger" size="mini"
                              @click="delComment(item.commentInfo.id)">删除
                  </van-button>
                  <div class="left">
                    <Avatar icon="ios-person" size="small" :src="item.userInfo.avatar"/>
                  </div>
                  <div class="right">
                    <div style="font-size: 1.3rem;" class="title ellipsis">
                      {{ item.commentInfo.createdAt }}
                      <span>({{item.testInfo.title}})</span>
                    </div>
                    <div class="info">
                      <span>{{ item.userInfo.username || '未知' }}</span>
                    </div>
                  </div>
                </div>
                <div class="content">
                  {{ item.commentInfo.content }}
                </div>
              </li>
            </ul>
            <van-divider dashed>{{ commentList.length === 0 ? '暂无相关评论' : '已经到底了' }}</van-divider>
          </div>
        </section>
        <section v-else>
          <van-divider dashed>请选择一个题目</van-divider>
        </section>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import {addComment, delComment, getWorkBookInfo, getTestInfo, updateRecord, getTestComment, collect} from "../../../api";
import config from './../../../config'
import {Toast, Dialog} from "vant";
import {mapState} from 'vuex'
import moment from "moment";

export default {
  name: "courseDetail",
  data() {
    return {
      isPlay: false,
      activeName: 'base',
      workbookInfo: {},
      testList: [],
      selectTestInfo: {}, // 选择题目
      // 单选答案
      soloResult: '',
      // 多选答案
      moreResult: [],

      commentList: [],
      joinList: [],
      isJoin: false,
      playIndex: -1,
      playUrl: '',
      showTestInfo: {},
      commentInfo: {
        title: '',
        content: ''
      },
      answerInfo: {
        content: ''
      }
    }
  },
  computed: {
    ...mapState(['userInfo'])
  },
  mounted() {
    this.getWorkBookInfo();
  },
  methods: {
    // 显示答案
    showResult(){
      // eslint-disable-next-line no-unused-vars
      let {testInfo = {}, optionList = [], record} = this.selectTestInfo;
      let soloResult = this.soloResult;
      let moreResult = this.moreResult;
      // eslint-disable-next-line no-console
      // console.log(this.selectTestInfo);
      if(testInfo.type === '单选题'){
        if(!soloResult){
          return Toast('先选择一个选项');
        }
        let trueList = optionList.filter(i => i.isTrue === 1).map(i => i.id);
        // eslint-disable-next-line no-console
        console.log(trueList, soloResult);
        if(trueList[0] === soloResult){
          // 答对了
          Toast('答对了')
          this.updateRecord(record.id, 2)
        }else {
          Toast('答错了')
          this.updateRecord(record.id, 3)
        }
      }else if(testInfo.type === '多选题'){
        if(moreResult.length === 0){
          return Toast('先选择一个选项');
        }
        moreResult.sort((a, b) => a - b);
        let trueList = optionList.filter(i => i.isTrue === 1).map(i => i.id);
        // eslint-disable-next-line no-console
        console.log(trueList, moreResult);
        if(JSON.stringify(trueList) === JSON.stringify(moreResult)){
          // 答对了
          Toast('答对了')
          this.updateRecord(record.id, 2)
        }else {
          Toast('答错了')
          this.updateRecord(record.id, 3)
        }
      }else if(testInfo.type === '简答题'){
        this.updateRecord(record.id, 2)
      }
    },
    async updateRecord(id, status){
      let data = await updateRecord({
        id, status
      });
      if (data.code === 200) {
        this.getTestInfo();
        this.getWorkBookInfo();
      } else {
        Toast(data.data);
      }
    },
    async showTest(zone, key) {
      if(!config.getCache('userId')){
        return Dialog.confirm({
          title: '尚未登录',
          message: '登录之后才可以答题？',
          confirmButtonText: '前往登录'
        }).then(() => {
          this.$router.push('/user/login');
        }).catch(() => {});
      }
      this.playIndex = key;
      // eslint-disable-next-line no-console
      console.log(zone.testInfo);
      this.showTestInfo = zone.testInfo;
      this.isPlay = true;
      this.soloResult = '';
      this.moreResult = [];
      this.getTestInfo();
    },
    async getTestInfo() {
      let data = await getTestInfo({
        userId: config.getCache('userId'),
        testId: this.showTestInfo.id
      });
      if (data.code === 200) {
        // eslint-disable-next-line no-console
        console.log(data.result);
        this.selectTestInfo = data.result;
        if(this.selectTestInfo.record.status !== 1){
          if(this.selectTestInfo.testInfo.type === '单选题'){
            this.soloResult = this.selectTestInfo.optionList.filter(i => i.isTrue === 1)[0].id
          }else if(this.selectTestInfo.testInfo.type === '多选题'){
            this.moreResult = this.selectTestInfo.optionList.filter(i => i.isTrue === 1).map(i => i.id)
          }
        }
        this.getWorkBookInfo();
        this.getTestComment();
      } else {
        Toast(data.data);
      }
    },
    async addComment() {
      let userId = config.getCache('userId');
      if (!userId) {
        return Dialog.confirm({
          title: '尚未登录',
          message: '登录之后才可以评论？',
          confirmButtonText: '前往登录'
        }).then(() => {
          this.$router.push('/user/login');
        }).catch(() => {
        });
      }
      let {content} = this.commentInfo;
      if (!content) {
        return Toast('评论内容不得为空');
      }
      let data = await addComment({
        testId: this.selectTestInfo.testInfo.id,
        userId,
        content
      });
      if (data.code === 200) {
        this.commentInfo = {
          title: '',
          content: ''
        };
        Toast(data.data);
        this.getTestComment();
      } else {
        Toast(data.data);
      }
    },
    async getTestComment(){
      let data = await getTestComment({
        testId: this.selectTestInfo.testInfo.id,
      });
      if (data.code === 200) {
        // eslint-disable-next-line no-console
        console.log(data.result);
        this.commentList = data.result.commentList.map(i => {
          i.commentInfo.createdAt = moment( i.commentInfo.createdAt).format('YY/MM/DD HH:mm')
          return i
        })
      } else {
        Toast(data.data);
      }
    },
    async collect(){
      let data = await collect({
        userId: config.getCache('userId') || '',
        testId: this.selectTestInfo.testInfo.id,
      });
      if (data.code === 200) {
        Toast(data.data);
        this.getTestInfo()
      } else {
        Toast(data.data);
      }
    },
    async getWorkBookInfo() {
      let {workbookId} = this.$route.params;
      Toast.loading({
        duration: 0, // 持续展示 toast
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner'
      });
      let data = await getWorkBookInfo({
        workbookId,
        userId: config.getCache('userId') || ''
      });
      Toast.clear();
      if (data.code === 200) {
        let {workbookInfo, testList} = data.result;
        this.workbookInfo = workbookInfo;
        this.testList = testList;
        // eslint-disable-next-line no-console
        console.log(workbookInfo, testList);
      } else {
        Dialog.alert({
          title: '出错了',
          message: data.data,
        }).then(() => {
          this.$router.back();
        });
      }
    },
    delComment(id) {
      Dialog.confirm({
        title: '删除提问',
        message: '确认删除此评论吗？',
        confirmButtonText: '立即删除'
      }).then(async () => {
        let data = await delComment({
          id
        });
        if (data.code === 200) {
          Toast(data.data);
          this.getTestComment();
        } else {
          Toast(data.data);
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
>>> .van-nav-bar {
  z-index 2 !important
}

.answer {
  width: 100%
  background-color: #f9f9f9

  > .answer-list {
    padding: 1rem
    margin-top: 2rem
    background-color: #fff

    > ul {
      width: 100%

      > li {
        width: 100%
        padding: 1rem
        border-bottom: .1rem solid #f1f1f1

        > .top {
          position relative
          width: 100%
          height: 5rem
          overflow: hidden;
          display flex

          > .left {
            max-width 3rem
            min-width 3rem
            flex 2
            display flex
            align-items: center;
            justify-content center
          }

          > .right {
            flex 8
            max-width 30rem
            padding: 0 0 0 .5rem

            > .title {
              color #616161
              font-size: 1.5rem
              height: 2.5rem

              > svg {
                position relative
                top: .2rem
                margin: 0 .5rem
                width: 1.5rem
                height: 1.5rem
              }
            }

            > .info {
              padding-left: .5rem

              > span {
                margin-right: 1rem
                color #919191
                font-size: 1.2rem
              }
            }
          }
        }

        > .content {
          padding-left: 4rem
        }

        > .children {
          padding-left: 4rem

          > ul {
            width: 100%

            > li {
              width: 100%
              background-color: #f9f9f9
              border-bottom .1rem solid #f9f9f9
              padding: .5rem
              font-size: 1.3rem
            }
          }
        }
      }
    }
  }
  position relative

  > .top {
    background-color: #fff

    > .submit {
      width: 100%
      height: 6rem
      position relative
    }
  }
}

.intro {
  width: 100%

  > .top {
    padding: 2rem
    background-color: #fff

    > .title {
      width: 100%
      font-family: PingFangSC-Medium;
      font-size: 2rem
      color: #2b333b;
    }

    > .zone {
      width: 100%
      padding: 1rem 0
      color #71777d
      font-size: 1.5rem
    }

    > .bottom {
      display flex
      align-items center
      justify-content space-between

      > .right {
        color #B0BDC9
        font-size: 1.3rem
      }
    }
  }

  > .bottom {
    padding: 1rem 2rem 2rem 2rem
    background-color: #fff
    margin-top: 1rem
  }
}

.zone-list {
  width: 100%
  padding: 1rem
  background-color: #fff

  > ul {
    width: 100%

    > li {
      width: 100%
      height: 4rem
      background-color: #f9f9f9
      margin-bottom: 1rem
      display flex
      align-items center
      padding: 0 1rem
      transition all .3s

      &.select {
        color #408ffb
      }

      > .left {
        height: 100%
        display flex
        align-items center

        > svg {
          width: 2rem
          height: 2rem
        }
      }

      > .middle {
        margin: 0 1rem
      }

      > .right {
        max-width 9rem
        min-width: 9rem;
        display flex
        height: 100%
        align-items: center;
        overflow: hidden;
        justify-content space-between
        color #848484

        svg {
          width: 1.8rem
          height: 1.8rem
        }
      }
    }
  }
}

.other {
  > .bottom {
    position fixed
    left: 0
    bottom: 0
    width: 100%
    height: 5rem
  }

  > .top {
    position relative
    width: 100%
    min-height: 20rem
    position relative
    background-color: #000
    color #fff
    .info{
      padding: 1rem
      >.bottom{
        margin-top: 1rem
      }
      >.jianda,>.danxuan,>.duoxuan{
        color #ccc
        padding: 1rem 0
      }
    }
    > img {
      position: absolute;
      left: 0
      top: 0
      width: 100%
      height: 100%
      object-fit cover
    }

    > video {
      position absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      object-fit contain
    }
  }
}
</style>
